import db from "libs/db"
import React from "react"
import Modal from "react-bootstrap/Modal"
import CurrencyInput from "react-currency-input-field"
import { AiOutlineDown } from "react-icons/ai"
import { BsDot } from "react-icons/bs"
import { toast } from "react-toastify"
import AdminServices from "services/api/AdminServices"
import ActivityLoaderMintbox from "views/mobileView/components/atoms/ActivityLoaderMintbox"
import { CurrencyFormat } from "views/mobileView/components/atoms/CurrencyFormat"
import ordinal_suffix_of from "views/mobileView/components/atoms/ordinal-suffix"
import cssVar from "views/mobileView/styles"
import NotFound from "./NotFound"

const SIPs = ({ navigation, TYPE, FUNDCODE, GOALID = 0, FUNDDATA }) => {
    const [selectedScheduleItem, setSelectedScheduleOption] = React.useState({})

    const [ORDERS, setORDERS] = React.useState([]);

    const [showModifySipModal, setShowModifySipModal] = React.useState(false)
    const [showSipAmountModal, setShowSipAmountModal] = React.useState(false)
    const [showDateModal, setShowDateModal] = React.useState(false);
    const [showStopSipModal, setShowStopSipModal] = React.useState(false);

    const [loading,setLoading]=React.useState(true);
    const [minimumAmount,setMinimumAmount] = React.useState(0);

    React.useEffect(async()=>{
        console.log(FUNDDATA)
        if(TYPE === 1){
            setMinimumAmount(FUNDDATA?.NAV > 500? FUNDDATA?.NAV * 1.2 : 500)
        } else if(TYPE ===3){
            setMinimumAmount(FUNDDATA?.MinSIPAmount)
        }
        await loadData();
    },[])

    React.useEffect(()=>{
        if(TYPE === 4){
            setMinimumAmount(selectedScheduleItem.Amount+1)
        }
    },[selectedScheduleItem])

    const loadData = async () => {
        try {
            db.get(db.model.user).then(async (p) => {
                await getPortfolio(p.userData.id)
            })
        } catch (error) {
            toast.error(error);
            setLoading(false)
        }
    };

    const getPortfolio = async (id) => {
        await db.get(db.model.portfolio).then(async (p) => {
            if (p === null) {
                let res = await AdminServices.getPortfolio(id).then(res => res);
                if (res.type === true) {
                    const a = await JSON.parse(res.data);
                    let aa = await a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                    console.log("Get portfolio API :", aa)
                    await db.set(db.model.portfolio, JSON.stringify(aa))
                    if (TYPE === 3) {
                        setORDERS(aa.ScheduledOrders.filter(s => s.MintboxId === FUNDCODE && s.GoalId === 0))
                    } else if (TYPE === 4) {
                        setORDERS(aa.ScheduledOrders.filter(s => s.GoalId === GOALID))
                    } else {
                        setORDERS(aa.ScheduledOrders.filter(s => s.ToFundCode === FUNDCODE))
                    }
                    setLoading(false)
                }
            } else {
                if (TYPE === 3) {
                    setORDERS(p.ScheduledOrders.filter(s => s.MintboxId === FUNDCODE && s.GoalId === 0))
                } else if (TYPE === 4) {
                    setORDERS(p.ScheduledOrders.filter(s => s.GoalId === GOALID))
                } else {
                    setORDERS(p.ScheduledOrders.filter(s => s.ToFundCode === FUNDCODE))
                }
                setLoading(false)
            }
        })
    }

    const updateScheduledOrder = async (key, val) => {
        setLoading(true)
        const data = {
            "uid": selectedScheduleItem.UID,
            "fromFundCode": selectedScheduleItem.FromFundCode,
            "toFundCode": key === "toFundCode" ? val : selectedScheduleItem.ToFundCode,
            "amount": key === "amount" ? val : selectedScheduleItem.Amount,
            "units": key === "units" ? val : selectedScheduleItem.Units,
            "orderDate": key === "orderDate" ? val : selectedScheduleItem.OrderDate,
            "isActive": key === "isActive" ? val : selectedScheduleItem.IsActive,
            "isDeleted": key === "isDeleted" ? val : selectedScheduleItem.IsDeleted
        }
        var res = await AdminServices.updateScheduledOrders(data).then(res => { return res })
        if (res.type) {
            toast.success('SIP updated')
            db.remove(db.model.portfolio)
            await loadData()
        } else {
            toast.error('Something went wrong! Try again.')
        }
    }

    const ModifySipModal = () => {
        const [selectedOption, setSelectedOption] = React.useState()

        const onConfirm = () => {
            switch (selectedOption) {
                case 'editAmount': {
                    setShowModifySipModal(false);
                    setShowSipAmountModal(true);
                    break;
                }
                case 'editDate': {
                    setShowModifySipModal(false);
                    setShowDateModal(true);
                    break;
                }
                case 'pauseSip': {
                    setShowModifySipModal(false);
                    updateScheduledOrder("isActive",selectedScheduleItem.IsActive === 1 ? 0 : 1)
                    break;
                }
                case 'stopSip': {
                    setShowModifySipModal(false);
                    setShowStopSipModal(true)
                    break;
                }
                default: setShowModifySipModal(false)
            }
        }
        return (
            <Modal
                className="bottom-drawer"
                show={showModifySipModal}
                onHide={() => setShowModifySipModal(false)}
                keyboard={false}
            >
                <Modal.Body>
                    {
                        selectedScheduleItem.IsActive === 1 && selectedScheduleItem.IsDeleted === 0 ?
                            <>
                                <div className="radio-item" style={{ marginTop: "6%" }}>
                                    <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="editAmount" id="editAmount" />
                                    <label className="bodyTextDark radio-label" htmlFor="editAmount">Edit SIP amount</label>
                                </div>
                                <div className="radio-item" style={{ marginTop: "6%" }}>
                                    <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="editDate" id="editDate" />
                                    <label className="bodyTextDark radio-label" htmlFor="editDate">Edit SIP date</label>
                                </div>
                            </>
                            :
                            <></>
                    }
                    {
                        selectedScheduleItem.IsDeleted === 0 ?
                            <div className="radio-item" style={{ marginTop: "6%" }}>
                                <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="pauseSip" id="pauseSip" />
                                <label className="bodyTextDark radio-label" htmlFor="pauseSip">{selectedScheduleItem.IsActive === 1 ? "Pause SIP" : "Resume SIP"}</label>
                            </div>
                            :
                            <></>
                    }
                    <div className="radio-item" style={{ marginTop: "6%" }}>
                        <input type="radio" name="fund" onChange={(e) => { setSelectedOption(e.target.value) }} value="stopSip" id="stopSip" />
                        <label className="bodyTextDark radio-label" htmlFor="stopSip" style={{ color: selectedScheduleItem.IsDeleted === 0 ? cssVar.getPropertyValue('--peach') : cssVar.getPropertyValue('--secondaryGreen') }}>{selectedScheduleItem.IsDeleted === 0 ? "Stop SIP" : "Restart SIP"}</label>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowModifySipModal(false)}>
                        Close
                    </button>
                    <button className="solidButton" onClick={() => onConfirm()}>
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    const StopSipModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showStopSipModal}
                onHide={() => setShowStopSipModal(false)}
            >
                <Modal.Body>
                    <div className='header1dark'>Are your sure you want to {selectedScheduleItem.IsDeleted === 1 ? "restart" : "stop"} SIP in {TYPE === 1? FUNDDATA?.ETFName :TYPE===2? FUNDDATA?.SchemeName: FUNDDATA?.MintboxName}?</div>
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowStopSipModal(false)}>
                        Close
                    </button>
                    <button className="solidButton"
                        onClick={() => {
                            updateScheduledOrder("isDeleted", selectedScheduleItem.IsDeleted === 1 ? 0 : 1)
                            setShowStopSipModal(false)
                        }}
                    >
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    const SipAmountModal = () => {
        const [s, setS] = React.useState()
        return (
            <Modal
                className="bottom-drawer"
                show={showSipAmountModal}
                onHide={() => setShowSipAmountModal(false)}
            >
                <Modal.Body>
                    <div className='header1dark'>Edit monthly SIP amount</div>
                    <div className='smallTextDark' style={{ marginTop: '2%' }}>Current SIP {CurrencyFormat(selectedScheduleItem.Amount)} | Min required {CurrencyFormat(Number(minimumAmount))}</div>
                    <CurrencyInput
                        className='normalInput rupee'
                        id="editSip"
                        name="editSip"
                        placeholder='20,000'
                        value={s}
                        allowDecimals={false}
                        allowNegativeValue={false}
                        disableAbbreviations={false}
                        prefix=' '
                        intlConfig={{ locale: 'en-IN', currency: 'INR' }}
                        onValueChange={(value) => setS(Number(value) || '')}
                    />
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowSipAmountModal(false)}>
                        Close
                    </button>
                    <button className="solidButton"
                        style={{ opacity: s !== '' && Number(s) >= Number(minimumAmount) ? 1 : 0.4 }}
                        onClick={() => {
                            if (s !== '' && Number(s) >= Number(minimumAmount)) {
                                updateScheduledOrder("amount", Number(s))
                                setShowSipAmountModal(false)
                            }
                        }}
                    >
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    const DateModal = () => {
        const [selectedDate, setSelectedDate] = React.useState(selectedScheduleItem.OrderDate)
        return (
            <Modal
                className="bottom-drawer"
                show={showDateModal}
                onHide={() => setShowDateModal(false)}
                keyboard={false}
            >
                <Modal.Body>
                    <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                        {
                            [1, 2, 3, 4, 5, 6, 7].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                        }
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                        {
                            [8, 9, 10, 11, 12, 13, 14].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                        }
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                        {
                            [15, 16, 17, 18, 19, 20, 21].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                        }
                    </div>
                    <div style={{ display: "flex", justifyContent: "center", gap: "20px", paddingTop: "20px" }}>
                        {
                            [22, 23, 24, 25, 26, 27, 28].map(d => <div key={d} onClick={() => setSelectedDate(d)} style={{ border: "1px solid #e5e5e5", borderRadius: '32px', height: "32px", width: '32px', textAlign: "center", paddingTop: "2px", color: d === selectedDate ? "#ffffff" : "#6c6c6c", background: d === selectedDate ? cssVar.getPropertyValue('--darkGreen') : 'transparent' }}>{d}</div>)
                        }
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <button className='outlineButton' onClick={() => setShowDateModal(false)}>
                        Close
                    </button>
                    <button className="solidButton" onClick={() => {
                        if (selectedDate !== selectedScheduleItem.OrderDate) {
                            updateScheduledOrder("orderDate", selectedDate)
                        }
                        setShowDateModal(false)
                    }}>
                        Confirm
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    if(loading){
        return <ActivityLoaderMintbox />
    }
    return (
        <div style={{ marginTop: '2%' }}>
            {
                ORDERS?.length === 0 ?
                    <NotFound text="You don't have any active SIPs" buttonText='Start SIP now' onClick={() => {
                        if(TYPE === 4){
                            
                        } if(TYPE === 1){
                            navigation.push({
                                pathname: '/portfolio/confirm-invest',
                                state: {
                                    fundData: {...FUNDDATA,MinSIPAmount:FUNDDATA.NAV},
                                    type: TYPE,
                                    sip: true
                                }
                            })
                        } else {
                            navigation.push({
                                pathname: '/portfolio/confirm-invest',
                                state: {
                                    fundData: FUNDDATA,
                                    type: TYPE,
                                    sip: true
                                }
                            })
                        }
                    }} />
                    :
                    ORDERS?.map((t, i) => {
                        return (
                            <>
                                <div key={i} style={{ padding: "4%", borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
                                    <div className='chip-sip' style={{ marginBottom: "1%" }}>
                                        <BsDot size={24} color={t.IsDeleted === 1 ? cssVar.getPropertyValue('--red') : t.IsActive === 0 ? cssVar.getPropertyValue('--gold') : cssVar.getPropertyValue('--secondaryGreen')} style={{ marginLeft: "-10px" }} />
                                        {t.IsDeleted === 1 ? 'Stopped' : t.IsActive === 0 ? 'Paused' : 'Active'}
                                    </div>
                                    <div style={{ display: "flex", alignItems: "center", gap: "6px" }}>
                                        <div style={{ flex: 1 }}>
                                            <div className='smallTextLight' style={{ marginBottom: "1%" }}>
                                                {ordinal_suffix_of(t.OrderDate)} of every {t.Frequency === 3?'three':t.Frequency===6?'six':t.Frequency===1?'twelve':''} month(s)
                                            </div>
                                            <div className='header1dark'>{CurrencyFormat(t.Amount)}</div>
                                        </div>
                                        <div className='header2dark secondaryGreen'
                                            onClick={() => {
                                                setSelectedScheduleOption(t)
                                                setShowModifySipModal(true);
                                            }}>Modify <AiOutlineDown size={12} /></div>
                                    </div>
                                </div>
                            </>
                        )
                    })
            }
            <ModifySipModal />
            <DateModal />
            <SipAmountModal />
            <StopSipModal />
        </div>
    )
}

export default SIPs